.App {
  text-align: center;
  background-image: -webkit-radial-gradient(#4f6875, #263238);
  background-image: -o-radial-gradient(#4f6875, #263238);
  background-image: radial-gradient(#4f6875, #263238);
  min-height: 100vh;
  color: white;
  display: -ms-flexbox;
  display: flex;
  -ms-flex-direction: column;
      flex-direction: column;
  -ms-flex-line-pack: center;
      align-content: center;
  -ms-flex-pack: center;
      justify-content: center;
}

section#switches-list {
  display: -ms-flexbox;
  display: flex;
  -ms-flex-direction: row;
      flex-direction: row;
  -ms-flex-wrap: wrap;
      flex-wrap: wrap;
  -ms-flex-pack: center;
      justify-content: center;
}

.switch {
  position: relative;
  display: block;
  margin: 1em;
  width: 150px;
  height: 60px;
  color: white;
  outline: 0;
  text-decoration: none;
  border-radius: 100px;
  border: 2px solid #546E7A;
  background-color: #263238;
  -webkit-transition: all 500ms;
  -o-transition: all 500ms;
  transition: all 500ms;
}

.switch:active {
  background-color: #1c2429;
}
.switch:hover:not(.switch--moving):after {
  background-color: #455A64;
}
.switch:after {
  display: block;
  position: absolute;
  top: 4px;
  bottom: 4px;
  left: 4px;
  width: calc(50% - 4px);
  line-height: 45px;
  text-align: center;
  text-transform: uppercase;
  font-size: 20px;
  color: white;
  background-color: #37474F;
  border: 2px solid;
  -webkit-transition: all 500ms;
  -o-transition: all 500ms;
  transition: all 500ms;
}

.switch--on:after {
  content: 'On';
  border-radius: 50px 5px 5px 50px;
  color: #66BB6A;
}

.switch--off:after {
  content: 'Off';
  border-radius: 5px 50px 50px 5px;
  -webkit-transform: translate(100%, 0);
      -ms-transform: translate(100%, 0);
          transform: translate(100%, 0);
  color: #f44336;
  left: -4px;
}

.switch--moving {
  background-color: #1c2429;
}
.switch--moving:after {
  color: transparent;
  border-color: #435862;
  background-color: #222c31;
  -webkit-transition: color 0s, border-radius 500ms, background-color 500ms, -webkit-transform 500ms;
  transition: color 0s, border-radius 500ms, background-color 500ms, -webkit-transform 500ms;
  -o-transition: color 0s, transform 500ms, border-radius 500ms, background-color 500ms;
  transition: color 0s, transform 500ms, border-radius 500ms, background-color 500ms;
  transition: color 0s, transform 500ms, border-radius 500ms, background-color 500ms, -webkit-transform 500ms;
}


